	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked: James' Web Development website","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/flash-player-set-to-include-hd-video/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/flashvideo-490716d4fbab1f7056c7e837dd7dd6b5-4e724.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 425px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 91.52941176470588%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Flash HD Video\"\n        title=\"\"\n        src=\"/static/flashvideo-490716d4fbab1f7056c7e837dd7dd6b5-4e724.png\"\n        srcset=\"/static/flashvideo-490716d4fbab1f7056c7e837dd7dd6b5-10bb2.png 158w,\n/static/flashvideo-490716d4fbab1f7056c7e837dd7dd6b5-8da86.png 315w,\n/static/flashvideo-490716d4fbab1f7056c7e837dd7dd6b5-4e724.png 425w\"\n        sizes=\"(max-width: 425px) 100vw, 425px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>Adobe has recently released the Beta of its' next major update to the Flash Player - on <a href=\"http://labs.adobe.com/wiki/index.php/Flash_Player:9:Update:H.264\" title=\"Open link in a new window\">Abobe Labs</a> - and this time with HD Video support. (Amongst other general improvements) From a few other reports I've come across, the final version is expected in a couple of months, and of course other Flash-based products like Adobe Air will also benefit from the same technology.</p>\n<p>It'll be interesting to see how fast the takeup is with sites like youtube or dailymotion. And compression? FLV (Flash Video) compression is awesome. (Stroke of genius by Macromedia - hiring Sorenson to do it) But I think full-on HD Video might be pushing the limits of it.</p>","frontmatter":{"title":"Flash Player - set to include HD Video","date":"August 23, 2007","tags":["flash","adobe","beta","hd_video"],"url":null,"image":null,"description":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/404-pages/","tags":["wordpress-plugins"],"title":"404 pages","date":"2007-08-19T20:29:16.000Z","description":null,"image":null}},"next":{"frontmatter":{"path":"/blog/alexkingorg-cool-wordpress-plugins/","tags":["wordpress-plugins"],"title":"Alexking.org - Cool Wordpress Plugins","date":"2007-08-27T09:41:06.000Z","description":null,"image":null}}}}

